#crossword {
  min-width: 970px;
  font-family: 'Roboto', Helvetica, Arial, sans-serif;
  text-align: left;
  color: #2c3e50;
  margin: 25px;
}
#crossword a {
  color: #f57c00;
  text-decoration: none;
}
#crossword .danger {
  color: #e53935;
}
#crossword ul {
  list-style: none;
  text-align: left;
}
#crossword ul li {
  margin-right: 20px;
}
#crossword .yellow {
  background: #f9a825;
}
#crossword .red {
  background: #f00;
  color: #fff;
}
#crossword .cross {
  background: rgba(215,190,253,0.788);
}
#crossword .grid {
  display: inline-table;
  border-radius: 0.5em;
  overflow: hidden;
  border: 1px solid #ccc;
  margin: 0 2em;
}
#crossword .grid.blanks input {
  cursor: pointer;
}
#crossword .grid .row {
  height: 40px;
}
#crossword .grid .row:not(:last-child) {
  border-bottom: 1px solid #ccc;
}
#crossword .grid .row .cell {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
  width: 40px;
  height: 40px;
}
#crossword .grid .row .cell:not(:last-child) {
  border-right: 1px solid #ccc;
}
#crossword .grid .row .cell input {
  text-align: center;
  width: 1em;
  height: 1em;
  font-size: 1.35rem;
  text-transform: uppercase;
  border: none;
  transition: all 0.2s;
  width: 100%;
  height: 100%;
  outline: none;
}
#crossword .grid .row .cell.blank {
  background-color: #aaa;
}
#crossword .grid .row .cell.blank input {
  background-color: inherit;
  cursor: default;
}
#crossword .grid .row .cell.start {
  background: #ff00be;
}
#crossword .grid .row .cell.active {
  font-weigth: bold;
  background: rgba(5,55,225,0.6);
}
#crossword .word-form {
  position: relative;
  padding-left: 35px;
  border-bottom: 1px solid #009688;
  margin-bottom: 10px;
  padding-bottom: 10px;
}
#crossword .word-form.next {
  background: #c2e6ea;
}
#crossword .word-form.nowords {
  background: #e53935;
}
#crossword .word-form.error {
  background: rgba(183,28,28,0.867);
}
#crossword .word-form .letter {
  float: left;
  width: 20px;
}
#crossword .word-form .letter input {
  width: 17px;
  text-align: center;
  text-transform: uppercase;
}
#crossword .word-form .letter.filled input {
  background: rgba(234,254,43,0.522);
  border: 2px solid #ccc;
}
#crossword .word-form .letter.focused input {
  background: #ffeb3b;
}
#crossword .word-form .question {
  width: 100%;
}
#crossword .word-form .question .textarea {
  margin-top: 10px;
}
#crossword .word-form .question .green-plus {
  display: inline-block;
  font-size: 2em;
  color: #008000;
  cursor: pointer;
}
#crossword .word-form .words-count {
  margin-top: 4px;
}
#crossword .word-form .modal-area {
  position: absolute;
  width: 183%;
  top: 100%;
  left: -30px;
  background: rgba(165,214,167,0.8);
  z-index: 10;
  padding: 20px 0;
  box-shadow: 0 3px 10px #d5a569;
}
#crossword .word-form .modal-area:after {
  content: '';
  border-left: 20px solid transparent;
  border-right: 20px solid transparent;
  border-bottom: 20px solid rgba(165,214,167,0.8);
  position: absolute;
  top: -20px;
  left: 20px;
}
#crossword .word-form .modal-area > .inner {
  position: relative;
}
#crossword .word-form .modal-area > .inner .controls {
  text-align: center;
}
#crossword .word-form .modal-area > .inner .controls .go-to-page {
  display: inline-block;
  margin: 0 50px;
  font-size: 1.35em;
}
#crossword .word-form .modal-area > .inner .suggested-list li {
  font-family: monospace;
}
#crossword .word-form .modal-area > .inner .clues-list li {
  width: 100%;
}
#crossword .word-form .modal-area > .inner .close {
  position: absolute;
  right: 12px;
  top: -25px;
  font-size: 1.2em;
  color: #e53935;
  font-weight: bold;
  text-decoration: none;
}
#crossword .word-form .index-number {
  position: absolute;
  right: calc(100% - 30px);
}
#crossword .word-form .word-form-overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(165,214,167,0.8);
  text-align: center;
}
#crossword .word-form .word-form-overlay .inner {
  padding-top: 15px;
}
#crossword .cell-item {
  display: inline-flex;
  position: relative;
  cursor: pointer;
  width: 34px;
  height: 35px;
}
#crossword .cell-item.hovered > input {
  background: rgba(165,214,167,0.8);
}
#crossword .cell-item.red > input {
  background: rgba(183,28,28,0.867);
}
#crossword .cell-item input {
  cursor: pointer;
  width: 31px;
  text-align: center;
  font-size: 1.25em;
  background: transparent;
  border: 1px solid #000;
  text-transform: uppercase;
}
#crossword .cell-item .word-start {
  position: absolute;
  top: 2px;
  right: 3px;
  font-size: 0.75em;
  font-weight: bold;
  color: #1fb1f3;
  width: 15px;
  height: 15px;
  text-align: right;
}
#crossword .builder-page {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap-reverse;
  align-items: flex-end;
}
#crossword .btn {
  height: 50px;
  width: 120px;
  background: rgba(165,214,167,0.8);
  border: 3px solid #ce93d8;
  text-transform: uppercase;
  font-size: 0.8em;
  font-weight: bold;
  color: #0f3810;
  outline: none;
}
#crossword .btn.editing {
  background: rgba(234,254,43,0.522);
}
#crossword .toolbox {
  padding: 0 20px;
  text-align: center;
}
#crossword .toolbox > button {
  margin: 5px auto;
  display: block;
}
#crossword .toolbox.blank {
  background: #000;
}
#crossword .toolbox .grid-list {
  position: absolute;
  right: 30px;
  top: 30px;
  height: 90%;
  overflow-y: auto;
  background-color: #fff;
  flex-wrap: wrap;
  flex-direction: row;
  width: 33%;
  justify-content: space-around;
  align-content: flex-start;
  align-items: center;
}
#crossword .toolbox .grid-list .grid-preview {
  margin: 0 5px 10px 5px;
  display: grid;
  cursor: pointer;
}
#crossword .toolbox .grid-list .grid-preview .col {
  margin: 0;
}
#crossword .toolbox .grid-list .grid-preview .cell {
  margin: 0 0 -1px -1px;
  width: 10px;
  height: 10px;
  border: 1px solid;
}
#crossword .toolbox .grid-list .grid-preview .cell.blank {
  background: #000;
}
#crossword .log {
  position: absolute;
}
#crossword .log pre {
  text-align: left;
}
#crossword .builder-grid.page-inner {
  white-space: nowrap;
}
#crossword .builder-grid.page-inner .cell {
  display: inline-flex;
  margin: -2px 0;
  padding: 0;
  width: 34px;
  height: 38px;
}
#crossword .builder-grid.page-inner .cell.filled {
  background: rgba(165,214,167,0.8);
}
#crossword > button {
  margin-bottom: 5px;
  display: block;
}
#crossword > div {
  display: flex;
  flex-direction: column;
}
#crossword .log {
  text-align: center;
}
#crossword .page-inner {
  margin-right: 20px;
  margin-bottom: 20px;
  padding: 0 10px;
}
#crossword .button {
  display: flex;
  background-color: #08f;
  border-radius: 3px;
  color: #fff;
  cursor: pointer;
  opacity: 0.8;
  transition: opacity 0.3s;
}
#crossword .button:hover {
  opacity: 1;
}
#crossword .cell.blank > div input {
  background: #000;
  border: 1px solid transparent;
}
#crossword input[type=range] {
  -webkit-appearance: none;
  height: 38px;
  margin: 10px 0;
  width: 100%;
}
#crossword input[type=range]:focus {
  outline: none;
}
#crossword input[type=range]:focus::-webkit-slider-runnable-track {
  background: #f001a9;
}
#crossword input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  height: 10px;
  cursor: pointer;
  animate: 0.2s;
  background: #eee;
  border-radius: 5px;
  transition: background 0.6s ease-out;
}
#crossword input[type=range]::-webkit-slider-thumb {
  height: 2em;
  width: 2em;
  border-radius: 50%;
  background: #08f;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -11px;
}
#crossword .forms-list-wrapper {
  display: inline-grid;
}
#crossword .forms-list-wrapper label {
  margin-bottom: 10px;
  display: block;
  font-size: 1.25em;
}
#crossword .controls {
  width: 300px;
  margin-left: 50px;
}
#crossword .builder-grid.page-inner .cell.filled > div.hovered > input {
  background: rgba(165,214,167,0.8);
}
#crossword .builder-form.page-inner {
  overflow-y: auto;
  overflow-x: hidden;
  height: 96vh;
}
@media screen and (max-width: 767px) {
  #crossword .page {
    flex-direction: column;
  }
  #crossword .page .page-inner {
    width: 100%;
  }
  #crossword .page .builder-grid {
    margin: 0 auto;
  }
}
.slide-left-enter-active,
.slide-left-leave-active,
.slide-right-enter-active,
.slide-right-leave-active {
  transition: transform 0.5s linear;
}
.slide-left-enter,
.slide-left-leave-to {
  transform: translate3d(100%, 0, 0);
}
.slide-right-enter,
.slide-right-leave-to {
  transform: translate3d(-100%, 0, 0);
}
/*# sourceMappingURL=src/App.css.map */